.home__Container{
  row-gap: 7rem;
}
.home__content{
  grid-template-columns: 116px repeat(2,1fr);
  padding-top: 5.5rem;
  column-gap: 2rem;
  align-items: center;
}
.home__social{
  display: grid;
  row-gap: 1rem;
  grid-template-columns: max-content ;
}
.home__social-icon{
  font: 1.25rem;
  color: var(--title-color);
}
.home__social-icon:hover{
  color: var(--title-color-dark);
}
.home__title{
  font-size: var(--big-font-size);
  margin-bottom: var(mb-0-25);
}
.home__hand{
  width: 38px;
  height: 38px;
  margin-left: 0.4rem;
}
.home__subtitle{
  position: relative;
  font-size: var(--h3-font-size);
  font-weight: var(--font-normal);
  padding-left: 4.5rem;
  margin-bottom: var(--mb-1);
}
.home__subtitle::before{
  content: '';
  position: absolute;
  width: 70px;
  height: 1px;
  background-color: var(--text-color);
  left: 0;
  top: 1rem;
}
.home__img{
  background: url('../../assets/wahed.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 300px;
  box-shadow: inset 0 0 0 9px rgba(255  255  255 / 30%);
  order: 1;
  justify-self: center;
  animation: profile_animate 8s ease-in-out infinite 1s;
}
@keyframes profile_animate {
  0%{
     border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
  50%{
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
 }
 100%{
  border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
}
}
.home__scroll{
  margin-left: 9.25rem;
}
.wheel{
  animation:   scroll 2s ease infinite;
}
@keyframes scroll {
  0%{
    transform: translateY(0);
  }
  30%{
    transform: translateY(30.75rem);
  }
}
.home__scroll-button{
  display: flex;
  align-items: center;
}
.home__scroll-name{
  color: var(--title-color);
  font-weight: var(--font-medium);
  margin-right: var(--mb-0-25);
  margin-left: 2px;
}
.home__scroll-arrow{
  font-style: 1.25rem;
  color: var(--title-color);
}
.home_description{
  min-width: 400px;
  margin-bottom: var(--mb-3);
}

/* For large devices */
@media screen and (max-width: 992px) {
  .container {
    margin-left: var(--mb-1-5);
    margin-right: var(--mb-1-5);
  }
  .home__content {
    grid-template-columns: 100px repeat(2,1fr);
    column-gap: 1.25rem;
}
.home__hand{
  width: 26px;
  height: 26px;
}
.home__img{
  height: 250px;
  width: 250px;
  box-shadow: inset 0 0 0 8px rgb(255 255 255 / 30%);
}
.home__subtitle{
  padding-left: 3.75rem;
  margin-bottom: var(--mb-1);
}
.home__subtitle::before{
  width: 42px;
  top: 0.8rem;
}
.home_description{
  max-width: inherit;
  margin-bottom: var(mb-2-25);
}
.home__scroll{
  margin-left: 7.5rem;
}
}

/* For medium devices */
@media screen and (max-width: 768px) {
  .home__content{
    grid-template-columns: 0.5fr 3fr;
    padding-top: 2.5rem;
  }
  .home__img{
    order: inherit;
    align-self: inherit;
  }
  .home__data{
    grid-column: 1/3;
  }
  .home__img{
    width: 200px;
    height: 200px;
  box-shadow: inset 0 0 0 6px rgb(255 255 255 / 30%);
  }
  .home__scroll{
    display: none;
  }
}

/* For small devices */
@media screen and (max-width: 350px) {
  .home__img{
    height: 180px;
    width: 180px;
  }
  .home__hand{
    height: 22px;
    width: 22px;
  }
}
